import { IMenuProps } from "interface/component";
import { Menu } from "primereact/menu";
import React, { forwardRef } from "react";
import { useTranslation } from "react-i18next";

const MenuBoxWithoutMemo: React.FC<IMenuProps> = forwardRef((props: any, ref: any) => {
  const { model, id, className, appendTo, baseZIndex, onBlur } = props;

  const { t } = useTranslation();

  const translateOptions = (model: any) => {
    if (Array.isArray(model)) {
      const translatedOptionsTemp: any = [...model];

      translatedOptionsTemp[0]?.items?.forEach((option: any) => {
        if (option.label) {
          option.label = t(option.label);
        }
        if (option._template) {
          option.label = option._template;
        }
      });
      return translatedOptionsTemp;
    } else {
      return;
    }
  };

  const translatedOptions = translateOptions(model);

  return (
    <div className="menu-items">
      <Menu
        {...props}
        model={translatedOptions}
        className={className}
        popup
        ref={ref}
        id={id}
        appendTo={appendTo}
        baseZIndex={baseZIndex}
        onBlur={onBlur}
      />
    </div>
  );
});

// ----------------------- Memoization -------------------------- //
const MenuBox = React.memo(MenuBoxWithoutMemo);

// -------------------------- redux ------------------------------ //

// --------------------------- export ---------------------------- //
export default MenuBox;
